.basepage {
     min-height: 100vh;
     display: flex;
     flex-wrap: wrap;
     justify-content: space-around;
     align-items: center;
}

.title {
     align-content: center;
}


.eportfoliopreview{
     height: 300px;
     width: 300px;
     display: flex;
     margin: 10px;
     flex-direction: column;
     justify-content: space-around;
     align-items: center;

     overflow: hidden;
     border: 5px solid #1a1a1a;
     background: #1a1a1a;
     box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.829);
     border-radius: 20px;
}

.eportfoliopreview:hover{
     background-color: #000000;
}

.eportfoliopreview * {
     color: rgb(211, 211, 211);
     font-family: Roboto;
     text-shadow: 0px 2px 8px rgba(0, 0, 0, 0.829);
}
   

     
